body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-inter: Inter;

  /* font sizes */
  --font-size-xs: 12px;
  --font-size-17xl: 36px;
  --font-size-base: 16px;
  --font-size-mini: 15px;
  --font-size-smi: 13px;
  --font-size-5xl: 24px;
  --font-size-sm: 14px;
  --font-size-4xl: 23px;
  --font-size-3xl: 22px;
  --font-size-lg: 18px;

  /* Colors */
  --color-aliceblue: #e3eaef;
  --color-darkslategray: #3f3f3f;
  --color-white: #fff;
  --color-black: #000;
  --color-silver-100: #c4c4c4;
  --color-mediumseagreen: #0acf97;
  --color-darkgray-100: #9facb2;
  --color-darkgray-200: #98a6ad;
  --color-darkturquoise: #02c0ce;
  --color-royalblue: #2d7bf4;
  --color-gray: #fcfcfc;

  /* Gaps */
  --gap-10xs: 3px;
  --gap-base: 16px;
  --gap-11xs: 2px;

  /* Paddings */
  --padding-7xs: 6px;
  --padding-mini: 15px;

  /* border radiuses */
  --br-31xl: 50px;
  --br-8xs: 5px;
  --br-4xs: 9px;
  --br-9xs: 4px;
  --br-11xs: 2px;
  --br-7xs: 6px;
  --br-lgi: 19px;

  /* Effects */
  --sombra-en-componentes: 0px 2px 5px rgba(0, 0, 0, 0.05);
}
